@import "../../settings";

#contentWindow__container{
    position: fixed;
    left: 0;
    top : 0;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    gap:5%;
    width: 100vw;
    height: 100vh;

    background-color: $background-color;
    overflow-y: auto;

    img{
        position: absolute;
        left: 0;
        top:0;
        z-index: -1;

        width: 100%;
        height: 100%;

        object-fit:cover;
        -webkit-mask-image: -webkit-gradient(linear, center top, center bottom, from(rgba(0,0,0,1)), to(rgba(0,0,0,0)));
    }

    .back__button{
        position : absolute;
        top:0.5em;
        left:1em;
        padding : 0.5em 1em;
        border-radius: 1em;
        color:white;
        background-color: $background-color2;

        &:hover{
            cursor: pointer;
        }
    }

    .statistics__row{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: row;

        width : 90vw;
        height : 4vh;
        border-radius: 2em;
        
        .statistic{
            display: flex;
            justify-content: center;
            align-items: center;
            width : 25vw;
            height: inherit;

            padding: 0.1em 0.2em;
            background-color: $background-color;
            color: $white;

            font-size: 0.8em;
        }
    }

    p{
        padding : 0.5em 1.3em;
        height:50vh;

        text-justify: inter-word;
        font-weight: bold;
        color : white;
        overflow-y: auto;
    }
    


}

.show{
    display:flex;
    animation : open 0.2s ease-out;
}

.hide{
    display: none;
}

@keyframes open {
    from{
        transform : translateX(100%)
    }
    to{
        transform: translateX(0%);
    }
}
