@import "../../settings";



.navbar__container{
    display: flex;
    justify-content: space-between;
    align-items: center;

    width : 100vw;
    height : 10vh;
    padding: 0 5%;

    background-color: $background-color;
    box-shadow: 0 0.5em 1em 0 rgba(0,0,0,.2);

    .navbar__title{
        font-size: 1.5em;
        font-weight: bold;
        letter-spacing: 0.1em;
        color : $primary-color;

        &:hover{
            cursor: pointer;
        }
    }
    

    .navbar__left,.navbar__center,.navbar__right{
        display: flex;
        align-items: center;
        justify-content: space-between;

        flex-direction: row;
    }

    .button__wrapper{
        display: none;
    }

    .menuButton{
        display: flex;
        align-items: center;
        justify-content: center;

        flex-direction: column;

        color: white;
        
        font-size: 1.5em;
        line-height: 40%;

        label{
            pointer-events: none;
        }
    }

    .menuList{
        visibility: hidden;
        display: flex;
        align-items: center;
        justify-content: center;

        flex-direction: column;

        position: absolute;
        top: 10vh;
        left: 0;

        z-index: 5;
        .menuList__options{
            margin: 0;
            padding: 0;
            box-sizing: border-box;
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 0;
            width: 100vw;
            height: 10vh;
            
            color: white;
            background-color: $background-color;

            font-weight: bold;
            text-decoration: underline;

            &:hover{
                cursor: pointer;
            }
        }
    }

    @media screen and (min-width: 1024px){
        padding: 0 2.5%;

        .navbar__right{
            width: 300px;
        }    
        
        .button__wrapper{
            display: block;
        }
        .menuButton{
            display: none;
        }

        .menuList{
            display: none;
        }
    }
}
