@import "./settings";

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    font-family: sans-serif;
    overflow-x:hidden;
}

body{
    background-color: $background-color;
    overflow-x:hidden;
    &::-webkit-scrollbar {
        width: 0.5vw;
    }
      
      /* Track */
    &::-webkit-scrollbar-track {
        background: #f1f1f1; 
    }
       
      /* Handle */
    &::-webkit-scrollbar-thumb {
        width: 10%;
        background: #888; 
    }
      
      /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
        background: #555; 
    }
}
