@import "../../settings";

.container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
    .heroSection{
        margin-top: 17.5vh;
        width: 100vw;
        height: 72.5vh;
        
        color : $white;

        font-size: 1em;
        text-align: center;

        padding : 0 5vw;

        p{
            color:rgb(213, 213, 213);
            b{
                font-size: 1.2em;
                color: white;
            }
        }
    }

    .infoSection{
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;

        width: 100vw;
        height : 150vh;
        background-color: $white;

        .infoCard__wrapper{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            
            width : 50vw;
            height : auto;

            text-align: center;

            .infoCard__title{
                font-size: 1.25em;
                font-weight: bold;
                margin-bottom: 1em;
            }
            .infoCard__text{
                font-size: 1.1em;
                text-align: center; 
                color: $background-color;
            }

            
        }
    }

    .recommendedAnimeSection, .recommendedMangaSection{
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-flow: column wrap;

        width: 100vw;
        height : auto;
        background-color: $white;
    }

    .recommendedTitle{
        margin : 5vh 0;
        font-size: 1.3em;
    }

    .animeWrapper, .mangaWrapper{
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-flow: row wrap;

        width: 100vw;
        height : auto;
    }
    @media screen and (min-width: 1024px){
        .heroSection{
            font-size: 1.25em;
            
            b{
                font-size: 1.5em;
            }
        }

        .infoSection{
            height: 100vh;
            padding: 5vh 0;
            .infoCard__wrapper{
                justify-content: center;
                width: 25vw;
                .infoCard__title{
                    font-size: 1.5vw;
                }
                .infoCard__text{
                    font-size: 1.5vw;
                }
            }
            
        }

        .recommendedTitle{
            font-size: 1.75em;
        }
        
    }
    
}

