@import "../../settings";

.searchbar__wrapper_main{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow : column wrap;
    
    width : 90vw;

    .searchbar__input{
        position: relative;
        width:inherit;
        height : 10vh;

        
        
        input{
            width: inherit;
            height: 7.5vh;
            padding: 0.5em 0.5em;

            &:focus, &:not(:focus):valid{
                ~.searchbar__input__label{
                    font-size: 5px;
                }
            }
        }
        margin-bottom: 2.5%;
    }
    
    
}

.searchbar__wrapper_secondary{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction : row;
    overflow: hidden;
    width : 75vw;
    height : 7.5vh;
    
    margin: 5vh 0;

    .button__wrapper{
        padding: 1em 1.5em;
        
        vertical-align: middle;
        text-align: center;
        height: inherit;
    }

    .button--primary{
        border: none;
    }

    .searchbar__input{
        position: relative;
        width:inherit;
        height : inherit;
        vertical-align: middle;
        
        white-space: nowrap;
        overflow: hidden;
        
        input{
            width: inherit;
            height: inherit;
            padding: 0.5em 0.5em;   

            outline: none;

            &:focus, &:not(:focus):valid{
                ~.searchbar__input__label{
                    font-size: 5px;
                }
            }
        }

        margin-bottom: 2.5%;
    }
    
}
