@import "../../settings";

.button__wrapper{
    padding: 0.75em 1.5em;
    color: white;
    overflow: hidden;
    &:hover{
        cursor: pointer;
    }
}

.button--primary{
    background-color: $primary-color;
    border: 0.1em solid $primary-color;

    .button__text{
        border: none;
        pointer-events: none;
    }
}

.button--secondary{
    background-color: transparent;
    border: 0.1em solid white;

    .button__text{
        border: none;
        pointer-events: none;
    }
}