@import "../../settings";

#loadingWindow__container{
    visibility: visible;
    position: fixed;
    top:0;
    left:0;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap : 5%;
    width: 100vw;
    height:100vh;

    background-color: $background-color;

    span{
        font-size: 2em;
        color : $primary-color;
    }
    .loading__bar{
        float: right;
        width: 0;
        height : 0.5vh;

        background-color: $secondary-color;

        animation: load 3s ease-in-out infinite;
    }
}

@keyframes load {
    0%{
        width: 0;
    }
    50%{
        width : 10vw;
    }
}

@keyframes hide {
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
        display: none;
        pointer-events: none;
    }
}