.contentCard{
    
    display: flex;
    justify-content: center;
    align-items: center;

    width : 55vw;
    height : 60vh;

    .contentCard__fullInfos{
        display: none;
    }

    .contentCard__img{
        width: 100%;
        height: 100%;
        img{
            display:block;
            margin : auto;
            width : 90%;
            height : 90%;
            object-fit:cover; 
            transition : 0.25s transform ease-out;
            position:relative;

            &:hover{
                -webkit-transform: scale(1.05) !important;
                -moz-transform: scale(1.05) !important;
                -o-transform: scale(1.05) !important;
                transform: scale(1.05) !important;
                transform:scale(1.05);
            }
        }
    }

   
}

@media screen and (min-width: 512px){
    .contentCard{
        width: 45vw;
        height: 75vh;
    }
}

@media screen and (min-width: 768px){
    .contentCard{
        width: 33vw;
        height: 75vh;
    }
}

@media screen and (min-width: 1024px){
    .contentCard{
        width: 25vw;
        height: 75vh;
    }
}


@media screen and (min-width: 1280px){
    .contentCard{
        width: 20vw;
        height: 75vh;
    }
}