@import "../../settings";


.carousel__wrapper{  
    width: 100%;  
    
    .carousel{
        .inner{        
            overflow-x: scroll;
            white-space: nowrap;

            .contentCard{
                display: inline-flex;
            }
            
            /* width */
            &::-webkit-scrollbar {
                height: 0.5vw;
            }
              
              /* Track */
            &::-webkit-scrollbar-track {
                background: #f1f1f1; 
                margin : 0 10vw;                
                border-radius: 2em;
            }
               
              /* Handle */
            &::-webkit-scrollbar-thumb {
                width: 10%;
                background: #888;          
                border-radius: 2em;
            }
              
              /* Handle on hover */
            &::-webkit-scrollbar-thumb:hover {
                background: #555; 
            }
            
        }
    }
}

